import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { User } from "./interfaces/user"

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  server_address:string='/api';

  private _loginUrl = "http://localhost:3100/login";

  registerUser(user: User) {
    //return this.http.post<any>("http://localhost:3100/register", {"user":user});
    return this.http.post<any>("${this.server_address}/register", {"user":user});
  }
  validateUserLogin(user: any) {
    return this.http.post<any>("${this.server_address}/login", user);
  }
  constructor(private http: HttpClient) { }

  loginUser(user: any) {
    return this.http.post<any>("${this.server_address}/login", user)
  }

  loggedIn() {
    return !!localStorage.getItem('token')
  }
  getToken() {
    return localStorage.getItem('token')
  }

  getUsers() {
    return this.http.get("${this.server_address}/users")
  }
  deleteUser(id: any) {
    return this.http.delete("${this.server_address}/removeUsers/" + id)
  }

  
  validateUser(user: any) {
    return this.http.post<any>("${this.server_address}/adminLogin", user);
  }

}
